<template>
  <div class="my">
    <div class="myPage">
      <h1>个人中心</h1>
      <!-- 头部 -->
      <div class="userinfo">
        <div class="back"></div>
        <div class="box">
          <div class="avator" :style="{'background-image': `url(${avator})`}"></div>
          <div class="content">
            <h3>
              <span class="name">{{userName}}</span>
              <p class="grade"><span>{{grade}}</span></p>
            </h3>
            <p class="text">{{intro}}</p>
          </div>
          <i class="iconfont">&#xe610;</i>
        </div>
      </div>
      <!-- 列表 -->
      <ul class="list">
        <!-- 我的发布 -->
        <router-link to="/my/myIssue/1" tag="li">
          <i class="iconfont left">&#xe6a0;</i>
          <span>我的发布</span>
          <i class="iconfont right">&#xe610;</i>
        </router-link>
        <!-- 我的收藏 -->
        <router-link to="/my/myIssue/0" tag="li">
          <i class="iconfont left">&#xe7ce;</i>
          <span>我的收藏</span>
          <i class="iconfont right">&#xe610;</i>
        </router-link>
        <li>
          <i class="iconfont left">&#xe60e;</i>
          <span>绑定邮箱</span>
          <i class="iconfont right">&#xe610;</i>
        </li>
        <!-- 个人信息 -->
        <router-link to="/my/userInfo" tag="li">
          <i class="iconfont left">&#xe63a;</i>
          <!-- <span>系统设置</span> -->
          <span>个人信息</span>
          <i class="iconfont right">&#xe610;</i>
        </router-link>
        <li>
          <i class="iconfont left">&#xe689;</i>
          <span>帮助中心</span>
          <i class="iconfont right">&#xe610;</i>
        </li>
      </ul>
      <!-- 退出登录 -->
      <div class="exit">
        <div class="btn" @click="login()">
          <span>{{state?'退出登录':'点击登录'}}</span>
        </div>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
export default {
  data () {
    return {
      state: false,
      avator: this.baseUrl + '/public/img/logo.png',
      userName: '每日美食',
      intro: '我的介绍',
      grade: 'LV0'
    }
  },
  methods: {
    login () {
      if (this.state) {
        const res = confirm('确定要退出吗？')
        if (res) {
          let userInfo = JSON.parse(localStorage.getItem('userInfo'))
          userInfo.state = false
          userInfo = JSON.stringify(userInfo)
          localStorage.setItem('userInfo', userInfo)
          this.$router.go(0)
        }
      } else {
        this.$router.push('/my/loginReg')
      }
    }
  },
  mounted () {
    let userInfo = localStorage.getItem('userInfo')
    if (!userInfo) { return false }
    userInfo = JSON.parse(userInfo)
    if (userInfo.state) {
      this.state = true
      this.userName = userInfo.userName
      this.avator = this.baseUrl + userInfo.avator
      this.intro = userInfo.intro
      this.grade = userInfo.grade
    }
  }
}
</script>
<style lang="less" scoped>
@import '~style/index.less';
.myPage{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  h1{
    font-size: .34rem;
    line-height: .88rem;
    text-align: center;
    color: #222;
  }
  .userinfo{
    height: 2.8rem;
    position: relative;
    overflow: hidden;
    .back{
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: -1;
      background: radial-gradient(#FFDEBE 0%, #fff 100%);
      filter: blur(40px);
    }
    .box{
      width: 6.9rem;
      height: 2rem;
      border-radius: .2rem;
      margin: .3rem auto 0;
      display: flex;
      align-items: center;
      color: #fff;
      background: linear-gradient(90deg,@orange 0%, @orange2 100% );
      .avator{
        width: .92rem;
        height: .92rem;
        border: 3px solid #fff;
        border-radius: 50%;
        background-size: cover;
        background-position: center center;
        margin-left: .41rem;
        margin-right: .29rem;
      }
      .content{
        flex: 1;
        h3{
          display: flex;
          align-items: center;
          .name{
            font-size: .34rem;
            line-height: 0.34rem;
          }
          .grade{
            height: .34rem;
            line-height: .34rem;
            margin-left: .14rem;
            padding: 0 .08rem;
            border-radius: .17rem;
            font-size: .12rem;
            color: @orange;
            background: #fff;
            text-align: center;
            letter-spacing: .02rem;
            display: flex;
            align-items: center;
            justify-content: center;
          }
        }
        .text{
          font-size: .16rem;
          margin-top: .18rem;
        }
      }
      .iconfont{
        margin-right: .4rem;
        font-size: .30rem;
      }
    }
  }
  .list{
    li{
      height: 1.04rem;
      border-top: 2px solid #fafafa;
      display: flex;
      align-items: center;
      padding: 0 .5rem;
      span{
        flex: 1;
        font-size: .28rem;
        color: #323232;
      }
      .left{
        font-size: .34rem;
        color: #0a0a0a;
        margin-right: .26rem;
      }
      .right{
        font-size: .26rem;
        color: #989898;
      }
    }
    li:nth-of-type(1){
      border-top: none;
    }
  }
  .exit{
    flex: 1;
    display: flex;
    align-items: center;
    background: #fafafa;
    .btn{
      width: 4.52rem;
      height: .9rem;
      line-height: .9rem;
      text-align: center;
      color: #fff;
      margin: auto;
      border-radius: .45rem;
      background: linear-gradient(90deg, @orange 0%, @orange2 100%);
      span{
        font-size: .3rem;
        font-weight: bold;
        letter-spacing: .08rem;
      }
    }
  }
}
</style>
